$active: #f8c255;

.hamburger {
    background: none;
    display: block;
    cursor: pointer;
    border: 3px solid $primary;
    transition: 500ms;
    &:hover, &:active, &:focus {
        border-color: $active;
        outline:none;
        .hamburger__element {
            background-color: $active;
            &::before, &::after { background-color: $active; }
        }
    }
}
.hamburger__element  {
    display: block;
    margin: 0 auto;
    width: 40px;
    height: 3px;
    background-color: $primary;
    transition: 500ms;


    &::before, &::after {
        content: '';
        width: 40px;
        height: 3px;
        background-color: $primary;
        display: block;
        margin: 0 auto;
        transition: 500ms;
        
    }
    &::before {
        transform: translateY(-10px);
        height: 3.1px;
    }
    &::after {
        transform: translateY(7px);
        height: 3.1px;
    }
  }

  .hamburger--active {
      position: relative;
      z-index: 1;
      border-color: $primary;
      &:hover {
        border-color: $black;
        .hamburger__element {
            background-color: $black;
            &::before, &::after { background-color: $black; }
        }
    }
        &:active, &:focus {
            border-color: $primary;
            .hamburger__element {
                background-color: $primary;
                &::before, &::after { background-color: $primary; }
            }
        }
      .hamburger__element {
        background-color: $primary;
        transform: rotate(45deg);
        height: 3px;
        &::before, &::after {
            background-color: $primary; 
            transform: rotate(-90deg);
            height: 3px;
            position: relative;
            z-index: 1;

        }
      }
    }

  
  