.footer {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

.footer__left {
    margin-left: 15.3%;
    margin-top: 20%;
}

.footer__right {
    margin-top: 20%;
    margin-right: 22%;
}
.footer__map {
    grid-column: span 1;
    background-color: yellow;
}
.footer__info {
    grid-column: span 1;
}


.footer__logo {
    display: block;
    max-width: 50%;
    margin: 0 auto;
}

.footer__slogan {
    font-size: 18px;
    color: rgb(57, 61, 60);
    text-transform: uppercase;
    text-align: center;
    line-height: 1;
    margin: 0;
    margin-top: 5px;

    @media(min-width: 1500px) {
        @include dynamicText(112.5%, 36, 18, 1500px, 3000, 1500);
    }
}

.footer__socials {
    margin-top: 60px;
    margin-left: 15.3%;
    margin-bottom: 15px;
}


.footer__address{
    font-size: 13px;
    color: $colorText;
    text-transform: uppercase;
    line-height: 1.077;

    @media(min-width: 1500px) {
        @include dynamicText(81%, 26, 13, 1500px, 3000, 1500)
    }
}

.footer__contacts {
    font-size: 13px;
    color: $colorText;
    text-transform: uppercase;
    line-height: 1.077;
    margin: 10% 0;

    @media(min-width: 1500px) {
        @include dynamicText(81%, 26, 13, 1500px, 3000, 1500)
    }
  }
  
.footer__tel, .footer__mail {
    color: $colorText;
    line-height: 1.077; 
    text-decoration: none;
    display: inline-block;
    transition-duration: 500ms;
    transition-property: color;

    &:hover {
        color: $activeColor;
    }
}

.footer__info {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.footer__copy {
    font-size: 12px;
    color: $colorText;
    line-height: 1.8;
    text-align: center;
    flex-basis: 100%;
    margin: 9% 0 7% 0;
    @media(min-width: 1500px) {
        @include dynamicText(75%, 24, 12, 1500px, 3000, 1500)
    }
}

@media(max-width: 1500px) {
    .footer__right {
        margin-right: 2%;
    }
}
@media(max-width: 768px) {
    .footer {
        grid-template-columns: 1fr;
    }
    .footer__map {
        padding-bottom: 75%;
    }

    .footer__left {
        margin-left: 5%;
    }
    .footer__right {
        margin-right: 5%;
    }
}

@media(max-width: 560px) {
    .footer__info {
        display: block;
    }

    .footer__left {
        margin-right: 5%;
        margin-top: 10%;
        text-align: center;
    }
    .footer__right {
        text-align: center;
        margin-left: 5%;
        margin-top: 5%;
    }

    .footer__logo {
        margin: 0 auto;
    }

    .footer__socials {
        margin-left: 0;
        justify-content: center;
    }
}