$bgNews: #d4edbf;

.news {
    display: grid;
    grid-template-columns: repeat(16, 1fr);
}

.news__left {
    grid-column: span 5;
    background-color: $bgNews;
    position: relative;
    &::before {
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        height: 100%;
        width: 100%;
        background-image: url('data:image/svg+xml;utf8, <svg xmlns="http://www.w3.org/2000/svg" width="100%" height="100%"><line x1="0" y1="100%" x2="100%" y2="0" stroke="rgb(255,255,255)" stroke-width="1"/></svg>');
    }
}

.news__title {
    font-size: 48px;
    color: $white;
    text-transform: uppercase;
    line-height: 1;
    font-weight: normal;
    margin: 62.3% 0 35.4% 46.2%;
    @media(min-width: 1500px) {
        @include dynamicText(300%, 96, 48, 1500px, 3000, 1500);
    }
}

.news__right {
    grid-column: span 11;
    display: flex;
    align-items: center;
    padding-left: 9.6%;
    padding-right: 15.9%;
}
.news__list {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
}
.news__element {
    flex-basis: 48%;
    display: flex;
    flex-direction: column;
    &:first-child {
        margin-right: 4%;
    }
}

.news__subtitle {
    font-size: 24px;
    font-family: "roboto_condensedbold";
    color: $colorText;
    text-transform: uppercase;
    line-height: 1;
    margin: 0;
    min-height: 50px;
    @media(min-width: 1500px) {
        @include dynamicText(150%, 48, 24, 1500px, 3000, 1500);
    }
}

.news__date {
    font-size: 12px;
    color: rgb(106, 105, 105);
    text-transform: uppercase;
    line-height: 1.2;
    margin: 0 0 4.5% 0;
    @media(min-width: 1500px) {
        @include dynamicText(75%, 24, 12, 1500px, 3000, 1500);
    }
    
}
.news__img {
    display: block;
    width: 100%;
}
  
.news__descriptions {
    font-size: 12px;
    color: rgb(106, 105, 105);
    line-height: 1.167;
    text-align: justify;
    margin: 5% 0 2% 0;
    @media(min-width: 1500px) {
        @include dynamicText(75%, 24, 12, 1500px, 3000, 1500);
    }
  }
  
  .news__href {
      text-align: right;
      margin: auto 0 0 0;
      
  }
  .news__link {
    font-size: 12px;
    color: $red;
    text-transform: uppercase;
    line-height: 1.167;
    text-decoration: none;
    @media(min-width: 1500px) {
        @include dynamicText(75%, 24, 12, 1500px, 3000, 1500);
    }
    &:hover {
        text-decoration: underline;
    }
}

@media(max-width: 1500px) {
    .news__right {
        padding-right: 2%;
    }
}
@media(max-width: 980px) {
    .news__right, .news__left {
        grid-column: span 16;
    }
    .news__right {
        padding-left: 2%;
        padding-top: 5%;
        padding-bottom: 5%;
        display: block;
    }
    .news__title {
        margin: 10% 0 30% 10%;
    }
}
@media(max-width: 768px) {
    .news__title {
        margin: 10% 0 40% 10%;
        font-size: 30px;
    }
}
@media(max-width: 560px) {
    .news__list {
      flex-direction: column;
    }
    .news__right {
        padding-left: 5%;
        padding-right: 5%;
    }
    .news__element {
        margin-bottom: 4%;
        flex-basis: auto;
        &:first-child {
            margin-right: 0;
        }
    }
    .news__subtitle {
        min-height: auto;
    }
}
  